import React from "react";
import LogoCodigoSentido from '../../assets/logo_codigo_sentido.svg'
import { Link } from "react-router-dom";

const Menu = () => {
  return (
    <div className="max-h-28 text-2xl text-white">
      <ul role="navigation" className="flex flex-row justify-center gap-12 py-8">
        <li>
          <Link to="/">Incio</Link>    
        </li>
        <li>
          <Link to="/lista_de_softwares" onClick={window.history.pushState("", "", "/")}>Softwares</Link>          
        </li>
        <img
        className="w-72"
        src={LogoCodigoSentido} 
        alt="Logotipo da Cdigo Sentido.  esquerda encontra-se o smbolo de colchete em azul claro; ao centro est escrito Cdigo Sentido, 
        sendo a palavra ''cdigo'' de cor branca e a palavra ''sentido'' da mesma cor que o colchete; 
         direta escontra-se o smbolo de chave muito presente na matemtica e possui a mesma cor da palavra ''cdigo''."
        />
        <li>
          <a href="#conteudos">Contedos</a>
        </li>
        <li>
          <a href="#sites">Sites</a>
        </li>
      </ul>
    </div>
  )
}

export default Menu;