import React from "react";
import Section from "../section";
import Card from "../card";
import Button from "../button";

const SectionList = () => {

  const didaticContentsList = [
    {
      name: 'CEaD IBC',
      description: 'Canal com intuito divulgativo de ensino de tecnologia para deficientes visuais.',
      contentLink: 'https://www.youtube.com/@ceadibc/featured'
    },
    {
      name: 'BlindTec Oficial',
      description: 'Canal com objetivo de comunicar acerca da tecnologia assistiva para deficientes visuais.',
      contentLink: 'https://www.youtube.com/@BlindTecOficial/streams'
    },
    {
      name: 'Curso de Windows 11 com o NVDA',
      description: 'Playlist com vdeos ensinando a navegar e utilizar o Windows 11 com NVDA.',
      contentLink: 'https://www.youtube.com/playlist?list=PLP1oneJ9pfptAffLDhOznVAKlGYQYHvbl'
    },
    {
      name: 'Curso de Microsoft Teams com o NVDA',
      description: 'Playlist com vdeos ensinando a navegar e utilizar o Microsoft Teams com NVDA.',
      contentLink: 'https://www.youtube.com/playlist?list=PLP1oneJ9pfpsDjgMk1oEgutjuhn38_wfD'
    },
    {
      name: 'Configurando Emacs',
      description: 'Playlist com intuito de orientar a configurar o Emacs.',
      contentLink: 'https://www.youtube.com/playlist?list=PLOQgLBuj2-3I7w8JQvCY8lbbrUZL-gf4m'
    }
]
  const webSitesList = [
  {
    name: 'BlindTec',
    description: 'O site da BlindTec visa a organizao de contedos de tecnologia didticos e acessveis para deficientes visuais.',
    siteLink: 'https://www.blindtec.com.br/blog/'
  },
  {
    name: 'Acessibilidade Legal',
    description: 'O site Acessibilidade Legal rene tpicos acerca da acessibilidade sobre a tecnologia web.',
    siteLink: 'http://www.acessibilidadelegal.com/13-landmarks.php'
  },
  {
    name: 'ChromeVox',
    description: 'ChromeVox  um leitor de tela para o google chrome. Para baixa-lo  necessrio clicar no boto "Usar no chrome" e depois em "Adicionar Extenso". Caso queira desativar a extenso  preciso ir ao Gerenciador de Extenses presente no menu do google.',
    siteLink: 'https://chromewebstore.google.com/detail/screen-reader/kgejglhpjiefppelpmljglcjbhoiplfn?hl=pt-BR&utm_source=ext_sidebar'
  },
  {
    name: 'Manual de uso NVDA',
    description: 'Este tpico se trata de um manual acerca do NVDA.',
    siteLink: 'https://www.guarapari.es.gov.br/pagina/ler/2078/acessibilidade-leitor-de-tela-nvda'
  },
  {
    name: 'Manual do EmacSpeak',
    description: 'Este tpico rene informaes diversas sobre tpicos do EmacSpeak.',
    siteLink: 'https://tvraman-github-io.translate.goog/emacspeak/manual/?_x_tr_sl=en&_x_tr_tl=pt-PT&_x_tr_hl=pt-PT&_x_tr_pto=tc'
  },
  {
    name: 'Sites diversos recomendados pela BlindTec',
    description: 'Listagem de variedades de sites teis disponibilizados pela BlindTec.',
    siteLink: 'https://www.blindtec.com.br/blog/sites-uteis/'
  },
  {
    name: 'Dicas de acessibilidade do Visual Studio',
    description: 'Este tpico visa a exposio de dicas de acessibilidade para o Visual Studio.',
    siteLink: 'https://learn.microsoft.com/pt-br/visualstudio/ide/reference/accessibility-tips-and-tricks?view=vs-2022'
  }
  ,
  {
    name: 'HandTalk',
    description: 'O site da Hand Talk abrange temas sobre acessibilidade.',
    siteLink: 'https://www.handtalk.me/br/ajudaacessibilidade/#navbar'
  },
  {
    name: 'Blog oficial do Emacspeak',
    description: 'Blog oficial do Emacspeak (com idioma em ingls).',
    siteLink: 'https://emacspeak.blogspot.com'
  }
]

  return (
    <div>
      <Section
        sectionTitle="Sobre"
        id="sobre"
        description={
        <div>
          O crescimento dirio do uso da tecnologia em diversos setores mundiais resultou na maior demanda por profissionais 
        na rea de tecnologia da informao (TI). Com um aumento de procura por trabalhadores do ramo tecnolgico, 
        o investimento e propagao da rea educacional para todos se tornou vital para construir um futuro congruente aos avanos ocorrentes.
          <br/>
          Partindo deste pressuposto, a acessibilidade e oportunidade de estudo e ingresso de pessoas PcD no ramo tecnolgico, 
          sobretudo os relacionados  programao, precisam ser ampliadas vigorosamente. Para os deficientes visuais, o estudo e trabalho 
          com desenvolvimento de cdigos pode tornar-se muito mais aproveitado e divulgado com as tecnologias j existentes 
          e as prximas que podem vir.
        </div>
        }
        bgGrey
      />

      <Section
        sectionTitle="Softwares"
        description={
        <div>
          Ao clicar no boto abaixo voc ser direcionado para a pgina com a seo de lista de softwares mais 
          atuantes no mercado para auxlio de manuseio de diversas ferramentas de tecnologia envolvendo o mundo da programao 
          para deficientes visuais. Cada bloco de software listado ter uma breve descrio da sua funo e logo 
          abaixo botes que redirecionam para acessar o site fonte e efetuar o download do software, bem como, 
          sites de orientao para instalao ou configurao do mesmo.
        </div>
        }
        buttonTitle="Ir para lista de softwares"
        link="/lista_de_softwares"
      />

      <Section
        sectionTitle="Contedos de mdia didticos"
        id="conteudos"
        description={
          <div>
            Esta seo possui cards contendo nome, descrio e boto de redirecionamento para o site de contedos 
            de mdia didticos referentes  programao.
            <br/>
            Os contedos didticos podem redirecionar para canais, playlists ou vdeos do youtube que contenham 
            objetivo educacional acerca de assuntos que envolvam tecnologia acessiva ou programao.
          </div>
          }
          bgGrey
          >
            <div className="grid grid-cols-3 gap-10 p-10">
              {didaticContentsList.map(content => {
                return <Card 
                title={content.name} 
                description={content.description} 
                >
                  <Button title="Redirecionar ao site" link={content.contentLink}/>
                </Card>
              })}
            </div>
      </Section>

      <Section
        sectionTitle="Sites didticos"
        id="sites"
        description={
        <div>
          A seguir segue uma lista de cards para sites ou tpicos de sites que seguem como referncia sobre acessibilidade tecnolgica 
          para deficientes visuais. Cada card ter o nome do site seguido abaixo de uma breve
          descrio acerca do objetivo principal dele mais um boto de redirecionamento para link do site.
        </div>
        }
        card
        >
          <div className="grid grid-cols-3 gap-10 p-10">
            {webSitesList.map(content => {
              return <Card 
              title={content.name} 
              description={content.description}
              >
                <Button title="Redirecionar ao site" link={content.siteLink}/>
              </Card>
            })}
          </div>
      </Section>
    </div>
  )
}

export default SectionList;